<template>
  <div class="all">
    <mu-paper :z-depth="1" class="demo-list-wrap">
       <mu-tabs
        :value.sync="active1"
        inverse
        color="secondary"
        text-color="#0080ff"
        center
        @change="tab(active1)"
      >
        <mu-tab>10</mu-tab>
        <mu-tab>24</mu-tab>
      </mu-tabs>
      <mu-divider></mu-divider>
      <mu-list v-if="active1 === 0">
      <div class="demo-text" style="font-weight:700;color:#000">
        <p>SHOW</p>
      </div>
      <mu-list>
        <mu-sub-header></mu-sub-header>
        <div class="main">
          <div>
            :
            <span>{{value1}}</span> min
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value1,time1)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            :
            <span>{{value6}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value6,up1)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            :
            <span>{{value3}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value3,down1)"
            />
          </div>
        </div>

        <div class="txt" >
					SHOW
			</div>
      </mu-list>
      </mu-list>




      <mu-list v-if="active1 === 1">
      <div class="demo-text" style="font-weight:700;color:#000">
        <p>SHOW</p>
      </div>
      <mu-list>
        <mu-sub-header></mu-sub-header>
        <div class="main">
          <div>
            :
            <span>{{value1_out}}</span> h
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value1_out,time1)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            :
            <span>{{value6_out}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value6,up1)"
            />
          </div>
        </div>
        <div class="main">
          <div>
            :
            <span>{{value3_out}}</span> %
            <img
              width="10px"
              src="../assets/baseline_edit_black_18dp.png"
              @click="openAlertDialog(value3,down1)"
            />
          </div>
        </div>

        <div class="txt" >
					SHOW
			</div>
      </mu-list>
      </mu-list>
      <mu-dialog
        title
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        {{name_label}}
        <!--  -->
        <mu-text-field type="number" v-if="active1==0" v-model="value2" placeholder></mu-text-field>
        <mu-text-field type="number" v-if="active1==1" v-model="value2_out" placeholder></mu-text-field>
        <mu-button v-if="active1==0" slot="actions" flat color="primary" @click="cencle(value2)"></mu-button>
        <mu-button v-if="active1==0" slot="actions" flat color="primary" @click="save(value2)"></mu-button>
        <mu-button
          v-if="active1==1"
          slot="actions"
          flat
          color="primary"
          @click="cencle(value2_out)"
        ></mu-button>
        <mu-button
          v-if="active1==1"
          slot="actions"
          flat
          color="primary"
          @click="save2(value2_out)"
        ></mu-button>
      </mu-dialog>
    </mu-paper>
  </div>
</template>

<script>
export default {
  data() {
    return {
      up:'',
      down:'',
      up1:'up',
      down1:'down',
      name_label: "",
      active1: 0,
      disabled: true,
      openAlert: false,
      readonly: true,
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      value1_out: "",
      value2_out: "",
      value3_out: "",
      value5_out: "",
      value6_out: "",
      value8_out: "",
      amount: "amount",
      quantity: "quantity",
      amount_add: "amount_add",
      quantity_add: "quantity_add",
      time: "",
      time1: "time",
      flag: "",
      visibility: false,
      events: false,
      calls: false,
      messages: false,
      notifications: false,
      sounds: false,
      videoSounds: false
    };
  },
  mounted() {
    this.get_fetch();
    this.get_fetch2();
  },
  methods: {
    tab(active1) {
      console.log(this.active1);
      this.get_fetch();
      this.get_fetch2();
    },
    openAlertDialog(q, t) {
      console.log(q + "q-----------------------");
      console.log(t + "t-----------------------");
      //q = this.value2;
      if(t == "time"){
        this.name_label = ":"
      }else if(t == "up"){
        this.name_label = ":"
      }else if(t == "down"){
        this.name_label = ":"
      }
      this.value2 = "";
      this.value2_out = "";
      this.flag = t;
      this.openAlert = true;
    },
    cencle(e) {
      console.log("");
      this.openAlert = false;
    },
    save(q1) {
      console.log(this.flag)
      if (this.flag == "time") {
        console.log("time");
        var push_data = {
            time: '00'+this.value2, //
            up: this.value6, //
            down: this.value3, //
            tableflag:0
        };
      } else if (this.flag == "up") {
        console.log("up");
        var push_data = {
            time: '00'+this.value1, //
            up: this.value2, //
            down: this.value3,
            tableflag:0 //
        };
      } else if (this.flag == "down") {
        console.log("down");
        var push_data = {
            time: '00'+this.value1, //
            up: this.value6, //
            down: this.value2,
            tableflag:0 //
        };
      }
      this.fetch(push_data);
      this.openAlert = false;
    },
    save2(q1) {
      console.log(this.flag)
      if (this.flag == "time") {
        console.log("time");
        var push_data = {
            time: this.value2_out * 60, //
            up: this.value6_out, //
            down: this.value3_out, //
            tableflag:1
        };
      } else if (this.flag == "up") {
        console.log("up");
        var push_data = {
            time: this.value1_out * 60, //
            up: this.value2_out, //
            down: this.value3_out,
            tableflag:1 //
        };
      } else if (this.flag == "down") {
        console.log("down");
        var push_data = {
            time: this.value1_out * 60, //
            up: this.value6_out, //
            down: this.value2_out,
            tableflag:1 //
        };
      }
      this.fetch2(push_data);
      this.openAlert = false;
    },
    fetch(push_data) {
      console.log(push_data);
      var that = this;
      this.$http
        .post(
          "http://trendapi.show-fund.com/update_alarm_config/",
          push_data
        )
        .then(function(res) {
          console.log(res);
            that.get_fetch();
        });
    },
    get_fetch() {
      var that = this;
      this.$http
        .get(
          "http://trendapi.show-fund.com/get_alarm_config/0/"
        )
        .then(function(res) {
          console.log(res.data)
          that.value1 = Number(res.data.time.split(":")[0])* 60 + Number(res.data.time.split(":")[1]) //
          that.value6 = res.data.up; //
          that.value3 = res.data.down; //
        });
    },
    fetch2(push_data) {
      console.log(push_data);
      var that = this;
      this.$http
        .post(
          "http://trendapi.show-fund.com/update_alarm_config/",
          push_data
        )
        .then(function(res) {
          console.log(res);
            that.get_fetch2();
        });
    },
    get_fetch2() {
      var that = this;
      this.$http
        .get(
          "http://trendapi.show-fund.com/get_alarm_config/1/"
        )
        .then(function(res) {
          console.log(res.data)
          that.value1_out = Number(res.data.time.split(':')[0]) +  Number(res.data.time.split(":")[1]); //
          that.value6_out = res.data.up; //
          that.value3_out = res.data.down; //
        });
    }
  }
};
</script>
<style scoped>
.txt{
    text-align: left;
    margin-top: 20px;
    font-size: 16px;
    padding: 15px;
}
.all {
    font-size: 20px;
}
.main {
  padding: 0px 16px;
  text-align: left;
}
.demo-list-wrap {
  width: 100%;
  overflow: hidden;
}
</style>



